.find {
  height: 18px;
  width: 18px;
  position: absolute;
  top: 14px;
  right: 15px;
  cursor: pointer; }

.find:before {
  border: 2px solid #ccc;
  border-radius: 50%;
  box-sizing: border-box;
  left: 0;
  top: -7px;
  height: 14px;
  width: 14px;
  content: '';
  display: block;
  position: absolute; }

.find:after {
  background: #ccc;
  bottom: 5px;
  transform: rotate(-45deg);
  right: 2px;
  height: 9px;
  width: 2px;
  content: '';
  display: block;
  position: absolute; }

.result-search {
  position: absolute;
  top: 100%;
  z-index: 2;
  width: 100%; }

.result-search ul {
  background-color: #fff;
  box-shadow: 0px 6px 10px 0px #ff6175; }

.result-search ul li a {
  display: flex;
  color: #4a4a4a;
  padding: 3px 10px;
  align-items: center; }


.search-wrap {
  position: relative;
  /*height: 32px*/
  margin: 6px 5px;
  display: inline-flex; }

.search-wrap .form-text {
  flex: 1;
  padding: 0 0 0 12px;
  border: 1px solid; }

.search-wrap .result {

  position: absolute;
  top: 96%;
  z-index: 2;
  width: 85%;
  left: 6%; }
.search-wrap .result ul {

  background-color: white; }

.search-wrap form {
  background: #fff;
  border: 0;
  border-radius: 20px;
  padding: 0px 13px 0px 16px;
  font: 14px/17px 'Proxima Nova', sans-serif;
  color: #343133;
  height: 32px; }


.search-wrap .form-submit {
  border: 0;
  position: absolute;
  top: 4px;
  right: 5px;
  cursor: pointer;
  height: 77%;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center; }
