///*************************************
//*******      MOBILE PANEL        ******
//**************************************/

label[for="toggle-button"] {
  position: absolute;
  top: -9px;
  left: 0px;
  color: #fff;
  font-size: 21px;
  padding: 7px 9px;
  cursor: pointer;
  z-index: 4; }

#panel {
  width: 100%;
  display: flex;
  flex-flow: column;

  height: 100%; }

#panel header .wrap {
  box-shadow: 0 5px 18px 0 #d2d2d2;
  min-height: 100vh;
  margin: 0 0 -83px 0; }


input#toggle-button {
  display: none; }


/************      MENU ***************/
nav#menu {
  max-width: 0px;
  min-width: 0px;
  position: relative;
  overflow: hidden;
  display: none; }

#toggle-button:checked~nav#menu {
  min-width: 400px;
  max-width: 400px; }


#toggle-button:checked~label[for="toggle-button"] {
  position: absolute;
  text-align: center;
  padding: 1px;
  left: 0px;
  top: 0px;
  width: 30px;
  height: 30px;
  color: #000;
  z-index: 3;
  border: 1px solid #e30000; }

.menu-wrap {
  margin: 50px 20px 0 20px;
  width: 360px; }

.menu-wrap .item {
  padding:  0;
  transition: .3s;
  text-transform: uppercase; }


#toggle-button:checked ~#menu .menu-wrap .item {

  padding:  10px 0;
  position: relative; }


.transition {
  transition: .3s; }


