
.form-container {
  display: flex;
  flex-flow: column wrap;
  padding: 35px 30px 15px 30px;
  background: #eee;
  width: 300px;
  margin: 3% auto;
  align-items: stretch; }

.form-title {
  font-weight: bold;
  font-size: 120%;
  text-align: center;
  color: #949494;
  margin-bottom: 20px; }

.form-input {
  background: #f7f7f7;
  border-radius: 0;
  border-bottom: 1px solid #ccc;
  border-left: 0;
  border-top: 0;
  border-right: 0;
  flex: 1;
  margin: 10px 0;
  padding: 12px;
  width: 100%;
  box-sizing: border-box;
  color: #757575; }

.submit {
  padding: 14px;
  background: rgba(0, 0, 0, 0.15);
  text-align: center;
  font-weight: 600; }

.submit:hover {
  background: #a2a2a2;
  color: #fff; }

.bottom {
  display: flex;
  align-items: center; }

.bottom li {
  flex: 1;
  text-align: center;
  display: flex; }

.bottom li a {
  color: #949494;
  flex-basis: 100%;
  padding: 10px 3px; }

.bottom li a:hover {
  color: #fff;
  background-color: #a2a2a2; }


