@import "../../var";
.top-menu a {
  color: #fff; }

.top-menu > .row {
  background: $color-main;
  color: #fff;
  height: 100%;
  justify-content: space-between;
  align-items: center;
  max-width: 1310px;
  margin: auto; }

.user-menu {
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  height: 100%;
  min-height: 25px;
  padding: 0 48px;
  position: sticky;
  top: 0;
  z-index: 501;

  &:before {
    content: '☰';
    display: block;
    padding: 0 8px;
    align-self: center; }
  &:hover {
    background-color: #3da58f;
    &>span> .nav,
    &>span> .nav a,
    &>.nav,
    &>.nav a {

      display: flex; } } }

.user-menu .icon {
  padding: 5px 16px;
  display: flex; }

.user-menu .icon svg {
  width: 14px;
  height: 14px; }

.user-menu > .row {
  height: 100%;
  flex-basis: 280px;
  justify-content: flex-end;
  align-items: stretch; }

.user-menu > a {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%; }

.user-menu a {
  color: #8d8d8d; }

.user-menu > span.row > a {
  color: #fff;
  display: flex; }

.contacts a {
  color: #fff; }

.contacts {
  color: #fff;
  align-self: center;
  margin-left: 50px; }

.logo {
  display: flex;
  flex: 1;
  justify-content: start;
  margin-left: 10px; }

.logo img {
  width: 100%;
  max-width: 35%;
  min-width: 50px; }

.logo a {
  width: 22vw;
  display: block; }

.logo > span {
  margin: 3px 0 0 -50px;
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  color: #343133; }


.h-phone,
.h-upper > div:not([class]) {
  position: relative;
  top: 8px;
  white-space: nowrap; }




.FIO {
  display: flex;
  align-items: center;
  padding: 0 10px;
  font-size: 12px; }

//.user-menu>span:after
//  content: '☰'
//  display: block
//  padding: 0 8px


.form-submit input {

  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0; }

.user-button {
  flex-direction: column; }

.user-button div:first-child span {

  padding-left: 10px;
  color: #fff; }

.nav {
  display: none;
  flex-flow: column;
  border: 1px solid #eee;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background: #fff;
  z-index: 2;
  position: absolute;
  top: 100%;
  right: -1px;
  width: 280px; }

.nav a:hover {
  background: #ababab33; }

.nav a {
  padding: 5px 10px; }

.user-menu:hover > span > .nav {
  display: flex; }

/***** top menu */
.top-menu .item {
  padding: 0 10px; }

/***** Всплывающие телефоны */
.phone-wrap {
  align-items: center;
  display: flex; }

.icon-phone {
  position: relative;
  display: flex;
  align-items: center;
  font: bold 17px/21px 'Proxima Nova', sans-serif;
  padding: 0 25px;
  white-space: nowrap; }

.icon-menu {
  padding: 3px; }

.icon-phone > a {
  position: relative;
  color: #4e4e4e; }

.icon-phone > a:before {
  content: '\1F57B';
  width: 26px;
  height: 26px;
  background-position: 0 -44px;
  position: absolute;
  left: -20px;
  top: -3px;
  color: #e30000;
  font-size: 27px;
  transform: rotate(-30deg); }


.icon-phone > a:after {
  content: '';
  display: block;
  position: absolute;
  top: 3px;
  right: -21px;
  width: 8px;
  height: 8px;
  border-bottom: 3px solid #e30000;
  border-right: 3px solid #e30000;
  transform: rotate(45deg); }

.icon-phone .popup-info {
  position: absolute;
  left: 0;
  top: 100%;
  padding-top: 0;
  height: 0;
  z-index: 6; }

.icon-phone .popup-info .inner {
  width: 200px;
  background: #fff;
  -webkit-box-shadow: 0 10px 25px 0 rgba(0, 0, 0, 0.28);
  box-shadow: 0 10px 25px 0 rgba(0, 0, 0, 0.28);
  font: 15px/19px 'Proxima Nova', sans-serif;
  color: #181617;
  padding: 0 30px;
  height: 0;
  overflow: hidden;
  border-radius: 2px;
  position: relative;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s; }


.icon-phone .popup-info .inner:before {
  content: '';
  border-bottom: 10px solid #fff;
  border-left: 11px solid transparent;
  border-right: 11px solid transparent;
  position: absolute;
  left: 55px;
  top: -10px; }


.icon-phone .popup-info .head {
  border-bottom: 1px solid #ebebeb;
  padding-bottom: 15px;
  margin-bottom: 16px;
  white-space: initial; }

.icon-phone .popup-info p {
  margin: 0 0 11px; }

.icon-phone .popup-info p:last-child {
  margin: 0; }

.icon-phone .popup-info .phones a {
  font: bold 17px/21px 'Proxima Nova', sans-serif;
  color: #343133;
  line-height: 1.7; }

.icon-phone:hover .popup-info {
  padding-top: 15px;
  height: auto; }

.icon-phone:hover .popup-info .inner {
  padding: 25px 30px;
  height: auto;
  overflow: visible; }


#icon-locked {
  fill: #e30000; }


.h-upper div:not([class]) {
  margin-left: 45px; }


.h-phone {
  color: #343133;
  font: bold 21px/17px 'Proxima Nova', sans-serif; }

.header-lower span {
  padding-right: 8px; }

.h-upper {
  padding: 12px 0 10px 0;
  max-width: 1310px;
  margin: auto;
  display: flex;
  position: relative;
  height: 45px; }

.h-lower {
  padding-right: 10px;
  max-width: 1310px;
  margin: auto;
  display: flex;
  background: #000;
  height: 46px;
  justify-content: space-between; }

